<template>
  <div class="channel-edit">
    <!-- 我的频道 -->
    <van-cell :border="false">
      <div slot="title" class="title-text">我的频道</div>
      <van-button
        size="mini"
        round
        type="danger"
        plain
        @click="isEditShow = !isEditShow"
      >
      {{ isEditShow ? '完成' : '编辑' }}
      </van-button>
    </van-cell>

    <van-grid class="my-grid" :gutter="10">
      <van-grid-item
        class="channel-item"
        v-for="(channelItem, index) in myChannels"
        :key="index"
        @click="onMyChannel(channelItem, index)"
      >
      <template #icon>
        <!-- 判断右上角 叉是否显示，且第一个不显示叉 -->
        <van-icon name="close" v-show="isEditShow && index !==0 "></van-icon>
      </template>
      <span class="text" :class="{active: active === index}" slot="text">{{ channelItem.name }}</span>
      </van-grid-item>
    </van-grid>

    <!-- 频道推荐 -->
    <van-cell :border="false">
      <div slot="title" class="title-text">频道推荐</div>
    </van-cell>

    <van-grid :gutter="10">
      <van-grid-item
        class="channel-item"
        v-for="obj in recommendChannels"
        :key="obj.id"
        :text="obj.name"
        icon="plus"
        @click="addChannels(obj)"
      />
      </van-grid>

  </div>
</template>

<script>
import { mapState } from 'vuex'
import { setItem } from '@/utils/storage'
import { updateChannelAPI, deleteChannelAPI } from '../../../api/index.js'
export default {
  name: 'ChannelEdit',
  data () {
    return {
      myChannelsClone: this.myChannels,
      isEditShow: false
    }
  },
  /*
  从store状态管理器中 获取state数据的两种方式：
    1.this.$store.state.数据
    2.computed : {
      ...mapState(['数据'])
    }
  */
  props: {
    myChannels: {
      type: Array,
      default: () => []
    },
    active: {
      type: Number,
      default: 0
    },
    allChannels: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    // 添加频道
    async addChannels (newChannel) {
      // console.log(newChannel)
      // 不可以直接修改父组件传递过来的参数，所以拷贝一份在data中
      this.myChannelsClone.push(newChannel)

      // 数据持久化处理
      if (this.user) {
        // 已登录状态 调用接口
        try {
          const channel = [
            {
              id: newChannel.id,
              seq: this.myChannels.length - 1
            }
          ]
          await updateChannelAPI({
            channels: channel
          })
          this.$toast('添加成功')
        } catch (error) {
          this.$toast('添加失败')
        }
      } else {
        // 未登录状态 将数据保存到本地
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    },
    onMyChannel (channel, index) {
      // 1 判断是否是处于编辑状态
      if (this.isEditShow) {
        // 先判断是否是 推荐
        if (index === 0) return
        // 编辑状态，删除频道
        // 1 删除选中的频道
        this.myChannelsClone.splice(index, 1)
        // 2 让激活的频道索引 active - 1
        if (index <= this.active) {
          this.$emit('update-active', this.active - 1, true)
        }
        // 删除数据持久化
        this.deleteChannel(channel)
      } else {
        // 非编辑状态 - 切换频道
        this.$emit('update-active', index)
      }
    },
    async deleteChannel (delChannel) {
      if (this.user) {
        // 已登录状态
        try {
          await deleteChannelAPI(delChannel.id)
        } catch (error) {
          this.$toast('删除频道失败')
        }
      } else {
        // 未登录状态
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    }
  },
  /*  展示推荐频道列表
    所有频道 - 用户频道 = 推荐频道
    封装计算属性筛选数据
    遍历所有频道
    对每一个频道都判断：该频道是否属于我的频道
    如果不属于我的频道，则收集起来
    直到遍历结束，剩下来就是那些剩余的推荐频道
    */
  computed: {
    ...mapState(['user']), // 获取到user信息（token）
    // 计算属性会观测内部依赖数据的变化
    // 如果依赖的数据发生变化，则计算属性会重新执行
    recommendChannels () {
      // // 数组的 filter 方法：遍历数组，把符合条件的元素存储到新数组中并返回
      // const newList = this.allChannels.filter(channel => {
      //   // 数组的 find 方法：遍历数组，把符合条件的第1个元素返回
      //   return !this.myChannels.find(myChannels => {
      //     return myChannels.id === channel.id
      //   })
      // })
      // return newList
      // loadsh 方法：
      return [...this.$_.differenceBy(this.allChannels, this.myChannels, 'id')]
    }
  }
}
</script>

