  .channel-edit {
    .title-text {
      height: 100%;
      line-height: 58px;
      font-size: 32px;
      color: #333333;
    }

    .edit-btn {
      width: 104px;
      height: 48px;
      line-height: 48px;
      font-size: 26px;
      color: #f85959;
      border: 1px solid #f85959;
    }

    .channel-item {
      height: 86px;
      width: 160px;

      /* 设置按钮背景 */
      :deep(.van-grid-item__content)  {
        background-color: #f5f5f6;

        /* 设置按钮字体样式 */
        .van-grid-item__text,.text {
          color: #222;
          font-size: 28px;
          margin-top: 0;
        }
        // 设置 高亮
        .active {
          color: red;
        }
      }
    }
    // 频道推荐 加号 图标的样式处理
    :deep(.channel-item) {
      .van-grid-item__content {
        // flex 布局 所有的元素不会进行换行
        white-space: nowrap;
        flex-direction: row;

        /* 设置按钮字体样式 */
        .van-icon-plus {
          font-size: 28px;
          margin-right: 6px;
        }
      }
    }
    // 我的频道 右上角的删除按钮样式
    :deep( .my-grid) {
      .van-grid-item__content {
        .van-grid-item__icon-wrapper {
          position: absolute;
          right: -10px;
          top: -10px;
          font-size: 30px;
          color: #cacaca;
          z-index: 2;
        }
      }
    }

  }
</style>
