<template>
  <div class="home-container">
    <!-- 顶部搜索栏 -->
    <van-nav-bar class="page-nav-bar" fixed>
      <template #title>
       <van-button
        class="search-btn"
        type="info"
        round
        icon="search"
        to="/search"
        >搜索</van-button>
      </template>
    </van-nav-bar>
    <!-- 滑动导航栏 -->
    <!--
      v-model：绑定当前激活标签对应的索引值，默认启用第一个标签
      animated： 开启切换标签内容时的转场动画
      swipeable： 开启滑动切换标签页（一般体现在移动端）
      swipe-threshold： 设置最低滑动标签数 - 越小越好
     -->
    <van-tabs class="channel-tabs" v-model="active" animated swipeable swipe-threshold="2">
      <van-tab
      v-for="obj in channels"
      :key="obj.id"
      :title="obj.name"
      >
      <!-- 放一个内容组件列表 -->
      <ArticleList :channel="obj"></ArticleList>
      <!-- 放一个内容组件列表 -->
      </van-tab>
      <div slot="nav-right" class="placeholder"></div>
      <!-- 汉堡导航 -->
      <div slot="nav-right" class="hamburger-btn" @click="isChannelEditShow = true">
        <i class="toutiao toutiao-gengduo"></i>
      </div>

    </van-tabs>

    <!-- 弹出层 - 频道编辑 -->
    <van-popup
    class="edit-channel-popup"
    v-model="isChannelEditShow"
    position="bottom"
    :style="{ height: '100%' }"
    closeable
    close-icon-position="top-left">
    <!-- 弹出层里的内容 组件 -->
    <!-- 传递channels 传递active高亮索引-->
    <ChannelEdit
    :myChannels="channels"
    :active="active"
    :allChannels="allChannels"
    @update-active = "onUpdateActive"
    ></ChannelEdit>
    </van-popup>
    <!-- /频道编辑 -->

  </div>
</template>

<script>
import { getUserChannelsAPI, getAllChannelsNewAPI } from '@/api'
// import { getUserChannelsAPI, getAllChannelsAPI } from '@/api'
import ArticleList from './components/article-list.vue'
import ChannelEdit from './components/channel-edit.vue'
import { mapState } from 'vuex'
import { getItem } from '@/utils/storage'

export default {
  name: 'HomePage',
  components: {
    ArticleList,
    ChannelEdit
  },
  props: {},
  data () {
    return {
      active: 0,
      channels: [], // 频道列表
      isChannelEditShow: false, // 控制弹出层是否显示的变量
      allChannels: []
    }
  },
  computed: {
    ...mapState(['user'])// 获取到user信息（token） - 判断是否已经登陆
  },
  created () {
    this.loadChannels()// 获取我的频道
    // this.loadAllChannels()// 获取全部频道
    this.newLoadAllChannels()
  },
  methods: {
    // 获取用户当前频道列表
    async loadChannels () {
      try {
        // // 以后跳去接口返回值时，请使用解构赋值的形式拿到真实的返回参数
        // const { data: res } = await getUserChannelsAPI()
        // // console.log(res)
        // this.channels = res.data.channels
        let channelsTemp = []// 临时列表数据，最后会将他的赋值给 this.channels
        const localChannels = getItem('TOUTIAO_CHANNELS')// 获取本地列表
        if (this.user || !localChannels) {
        // 只要用户登录 或者没有本地存储时，就获取数据
          const { data: res } = await getUserChannelsAPI()
          channelsTemp = res.data.channels
        } else {
          channelsTemp = localChannels
        }
        // 将临时列表赋值给真实列表
        this.channels = channelsTemp
      } catch (err) {
        this.$toast('获取频道列表失败')
      }
    },

    // async loadAllChannels () {
    //   try {
    //     // 以后跳去接口返回值时，请使用解构赋值的形式拿到真实的返回参数
    //     const { data: res } = await getAllChannelsAPI()
    //     // console.log(res)
    //     this.allChannels = res.data.channels
    //   } catch (err) {
    //     this.$toast('获取全部频道列表失败')
    //   }
    // },

    async newLoadAllChannels () {
      const [err, res] = await getAllChannelsNewAPI()
      if (err) return this.$toast('获取全部频道列表失败')
      // console.log(res, err)
      this.allChannels = res.data.channels
      // console.log(this.allChannels)
    },

    // 自定义组件事件
    onUpdateActive (index, isShow = false) {
      // 修改active = index
      this.active = index
      // 将弹出层关闭
      this.isChannelEditShow = isShow
    }
  }
}
</script>

