<template>
  <div class="article-list">
    <!-- 下拉刷新 -->
    <van-pull-refresh
      v-model="isreFreshLoading"
      :success-text="refreshSuccessText"
      success-duration="1500"
      @refresh="onRefresh">
     <!-- 内容列表 -->
        <van-list
                v-model="loading"
                :finished="finished"
                :error.sync="error"
                error-text="请求失败，点击重新加载"
                finished-text="没有更多了"
                @load="onLoad"
                >
            <!-- <van-cell v-for="(item, index) in list" :key="index" :title="item.title" /> -->
          <ArticleItem
            v-for="(item,index) in list"
            :key="index"
            :article="item"
          ></ArticleItem>
        </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticlesAPI } from '../../../api/index.js'
import ArticleItem from '../../../components/article-item/article-item.vue'
export default {
  name: 'ArticleList',
  components: {
    ArticleItem
  },
  props: {
    channel: {
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      list: [], // 存储列表数据的数组
      loading: false, // 控制 加载状态的变量  如果说这个 loading 为true时，将不会触发@load 事件
      finished: false, // 控制数据加载结束的标志，当finished为true时，列表'van-list' 将不会触发 @load 同时 会展示 finished-text 的内容在列表的底部
      timestamp: null, // 请求下一页数据的时间戳
      error: false, // 控制列表加载失败的提示状态, error 为true时，@load不会触发
      isreFreshLoading: false, // 控制下拉刷新的 loading 状态
      refreshSuccessText: '刷新成功' // 下拉刷新成功提示文本
    }
  },
  methods: {
    // // 在初始化的时候 以及滚动到底部都是会触发@load 时间绑定的onload方法
    // onLoad () {
    //   // 1. 异步更新数据
    //   // setTimeout 仅做示例，真实场景中一般为 ajax 请求
    //   setTimeout(() => {
    //     for (let i = 0; i < 10; i++) {
    //       // 2，把请求结果的数据  拼接 到数组list的最后（push）
    //       this.list.push(this.list.length + 1)
    //     }

    //     // 3.本次数据加载结束以后，需要把加载状态 loading设置为结束状态
    //     // loading结束后 才会触发 下一次的@load事件
    //     this.loading = false

    //     // 4.通过 某个条件判断是否全部 数据加载完成，需要将finished设置为true 在此之后 @load 时间将 不会再触发
    //     if (this.list.length >= 40) {
    //       this.finished = true
    //     }
    //   }, 1000)
    // }

    // 初始化或滚动到底部的时候会触发调用 onLoad
    async onLoad () {
      // 1. 请求获取数据
      try {
        const { data: res } = await getArticlesAPI({
          channel_id: this.channel.id, // 频道 id
          timestamp: this.timestamp || Date.now() // 时间戳，请求新的推荐数据传当前的时间戳，请求历史推荐传指定的时间戳
        })
        // console.log(res)
        // 2. 把数据添加到 list 数组中
        this.list.push(...res.data.results)

        // 3. 设置本次加载中 loading 状态结束
        this.loading = false
        // 4. 判断数据是否加载结束
        if (res.data.results.length) {
          // 更新获取下一页数据的时间戳
          this.timestamp = res.data.pre_timestamp
        } else {
          // 没有数据了，设置加载状态结束，不再触发上拉加载更多了
          this.finished = true
        }
        // // 主动报错测试
        // JSON.parse('asd')
      } catch (err) {
        // 展示错误提示状态
        this.error = true

        // 请求失败了，loading 也需要关闭
        this.loading = false
        console.log(err)
        this.$toast('接口请求失败，请稍后再试')
      }
    },

    // 当下拉刷新的时候会触发调用该函数
    async onRefresh () {
      try {
        // 1. 请求获取数据
        const { data: res } = await getArticlesAPI({
          channel_id: this.channel.id, // 频道ID
          timestamp: Date.now() // 下拉刷线，每次请求获取最新数据，所以传递当前最新时间戳
        })

        // 2. 将数据追加到列表的顶部 - 使用unshift 添加到数组的最开始位置
        this.list.unshift(...res.data.results)

        // 3. 关闭下拉刷新的 loading 状态
        this.isreFreshLoading = false

        // 更新下拉刷新成功提示的文本
        this.refreshSuccessText = `刷新成功，更新了${res.data.results.length}条数据`
      } catch (error) {
        // 关闭下拉并且提示失败
        this.refreshSuccessText = '刷新失败'
        this.isreFreshLoading = false
      }
    }
  }
}
</script>

<style scoped lang="less">
.article-list {
  // 百分比单位是相对于父元素的，这里高度其实为0
  // height: 100%;
  // overflow-y: auto;

  // 视口（在移动端是布局视口）单位：vw 和 vh，不受父元素影响
  // 1vw = 视口宽度的百分之一
  // 1vh = 视口高度的百分之一
  /*
    兼容性：
    1. 在pc端已经兼容到IE9
    2. 移动端 IOS8以上， Android 4.4 以上
    3. 微信x5内核
  */
  height: 79vh;
  overflow-y: auto;
}
</style>
